import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, FormGroup, NgForm, FormBuilder, Validators } from '@angular/forms'
import { GroupService, IGroup } from '../../../../@core/data/group.service';

@Component({
    selector: 'ngx-edit-group-modal',
    templateUrl: './edit-group-modal.component.html',
})
export class EditGroupModalComponent implements OnInit {

    modalHeader: string;
    selectedRow: IGroup = {
        name: '',
        personGroupId: '',
        userData: '',
    };
    groupForm = new FormGroup({
        personGroupId: new FormControl(),
        name: new FormControl(),
        userData: new FormControl(),
    })



    constructor(private activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: GroupService,
    ) {

    }
    ngOnInit() {
        this.createForm();
    }

    createForm() {
        this.groupForm = this.fb.group({
            personGroupId: [{
                value: this.selectedRow.personGroupId,
                disabled: this.modalHeader.includes('Edit', 0),
            }, Validators.required],
            name: [this.selectedRow.name, Validators.required],
            userData: [this.selectedRow.userData, Validators.required],
        });
    }

    closeModal(message?: string) {
        this.activeModal.close(message);
    }

    save(form: NgForm) {
        const postBody = form.value;
        if (this.modalHeader.includes('Edit', 0)) {
            this.service.updateData(postBody).subscribe(data => {
                this.closeModal('Updated');
            });
        } else {
            this.service.saveGroup(postBody).subscribe(data => {
                this.closeModal('Saved');
            });
        }
    }
}



