import { Component } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { EditGroupModalComponent } from './modal/edit-group-modal.component';
import { GroupService, IGroup } from '../../../@core/data/group.service';

@Component({
    selector: 'ngx-person-group-list',
    templateUrl: './group-list.component.html',
    styles: [`
    nb-card {
      transform: translate3d(0, 0, 0);
    }
  `],
})
export class GroupListComponent {
    settings = {
        mode: 'external',
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
        },
        columns: {
            personGroupId: {
                title: 'Group ID',
                type: 'string',
            },
            name: {
                title: 'Group Name',
                type: 'string',
            },
            userData: {
                title: 'Comments',
                type: 'string',
            },
        },
    };

    source: LocalDataSource = new LocalDataSource();

    constructor(private service: GroupService,
        private modalService: NgbModal,
    ) {
        this.getGroupList();
    }

    getGroupList() {
        this.service.getGroup().subscribe(data => {
            this.source.load(data);
        });
    }

    onEdit(event): void {
        const activeModal = this.modalService.open(EditGroupModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = 'Edit Person Group';
        activeModal.componentInstance.selectedRow = event.data;
        activeModal.result.then((data) => {
            if (data === 'Updated') {
                this.getGroupList();
            }
        }, (reason) => {
        });
    }

    onDelete(event): void {
        if (window.confirm('Are you sure you want to delete?')) {
            const rowData: IGroup = event.data;
            this.service.deleteGroup(rowData).subscribe(data => {
                this.getGroupList();
            });
        }
    }

    onCreate(event): void {
        const activeModal = this.modalService.open(EditGroupModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = 'Add New Person Group';
        activeModal.result.then((data) => {
            if (data === 'Saved') {
                this.getGroupList();
            }
        }, (reason) => {
        });
    }
}



