import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { PersonService, IPerson } from '../../../../@core/data/person.service';
import { WebCamComponent } from 'ack-angular-webcam';

@Component({
    selector: 'ngx-edit-face-modal',
    templateUrl: './edit-face-modal.component.html',
})
export class EditFaceModalComponent implements OnInit {
    webcam: WebCamComponent;
    base64 = 'data:image/png;base64,';
    webcamOption = {
        audio: false,
        video: true,
        width: 50,
        height: 50,
        fallbackMode: 'callback',
        fallbackSrc: 'jscam_canvas_only.swf',
        fallbackQuality: 85,
    }

    modalHeader: string;

    selectedRow: IPerson = {
        name: '',
        personGroupId: '',
        userData: '',
        personId: '',
    };

    constructor(private activeModal: NgbActiveModal,
        private service: PersonService,
    ) {

    }
    onCamError(err) { }

    onCamSuccess() { }
    genPostData() {
        this.webcam.captureAsFormData({ fileName: 'file.jpg' })
            .then(formData => { })
            .catch(e => { })


        // img to file
        const block = this.base64.split(';');
        const contentType = block[0].split(':')[1];
        const realData = block[1].split(',')[1];
        const blob = this.b64toBlob(realData, contentType);
        const formDataToUpload = new FormData();
        formDataToUpload.append('file', blob);
    }

    genBase64() {
        this.webcam.getBase64()
            .then(base => {
                this.base64 = base;
            })
            .catch(e => console.error(e))
    }

    b64toBlob(b64Data, contentType, sliceSize?) {
        contentType = contentType || '';
        sliceSize = sliceSize || 512;

        const byteCharacters = atob(b64Data);
        const byteArrays = [];

        for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            const slice = byteCharacters.slice(offset, offset + sliceSize);

            const byteNumbers = new Array(slice.length);
            for (let i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }

            const byteArray = new Uint8Array(byteNumbers);

            byteArrays.push(byteArray);
        }

        const blob = new Blob(byteArrays, { type: contentType });
        return blob;
    }

    ngOnInit() {
    }

    closeModal(message?: string) {
        this.activeModal.close(message);
    }

    save() {
        const block = this.base64.split(';');
        const contentType = block[0].split(':')[1];
        const realData = block[1].split(',')[1];
        const blob = this.b64toBlob(realData, contentType);
        // const formDataToUpload = new FormData();
        // formDataToUpload.append('file', blob);

        const postBody: IPerson = {
            name: '',
            personGroupId: '',
            personId: '',
            userData: '',
        };
        postBody.personGroupId = this.selectedRow.personGroupId;
        postBody.personId = this.selectedRow.personId;
        if (this.modalHeader.includes('Edit', 0)) {
            // TODO:
            // this.service.updatePerson(postBody).subscribe(data => {
            //     this.closeModal('Updated');
            // });
        } else {
            this.service.createPersonFace(postBody, blob).subscribe(data => {
                this.closeModal('Saved');
            });
        }
    }
}



