import { NgModule } from '@angular/core';
import { Ng2SmartTableModule } from 'ng2-smart-table';

import { ThemeModule } from '../../@theme/theme.module';
import { ManagePersonRoutingModule, routedComponents } from './manage-person-routing.module';
import { WebCamModule } from 'ack-angular-webcam';

import { SmartTableService } from '../../@core/data/smart-table.service';
import { EditGroupModalComponent } from './group/modal/edit-group-modal.component';
import { EditPersonModalComponent } from './person/modal/edit-person-modal.component';
import { EditFaceModalComponent } from './person/modal/edit-face-modal.component';
import { ViewFaceModalComponent } from './person/modal/view-face-modal.component';
import { FaceRenderComponent } from './person/face-render.component';
import { UsualModule } from '../../@usual/usual.module';

@NgModule({
  imports: [
    ThemeModule,
    ManagePersonRoutingModule,
    Ng2SmartTableModule,
    WebCamModule,
    UsualModule,
  ],
  declarations: [
    ...routedComponents,
    EditGroupModalComponent,
    EditPersonModalComponent,
    EditFaceModalComponent,
    ViewFaceModalComponent,
    FaceRenderComponent,
  ],
  providers: [
    SmartTableService,
  ],
  entryComponents: [
    EditGroupModalComponent,
    EditPersonModalComponent,
    EditFaceModalComponent,
    ViewFaceModalComponent,
    FaceRenderComponent,
  ],
})
export class ManagePersonModule { }



