import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IPerson } from '../../../../@core/data/person.service';

@Component({
    selector: 'ngx-view-face-modal',
    templateUrl: './view-face-modal.component.html',
})
export class ViewFaceModalComponent implements OnInit {

    modalHeader: string;

    selectedRow: IPerson = {
        name: '',
        personGroupId: '',
        userData: '',
        personId: '',
        persistedFaceIds: [],
    };

    constructor(private activeModal: NgbActiveModal,
    ) {

    }

    ngOnInit() {
    }

    closeModal(message?: string) {
        this.activeModal.close(message);
    }
}



