import { Component, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl, FormGroup, NgForm, FormBuilder, Validators } from '@angular/forms'
import { PersonService, IPerson } from '../../../../@core/data/person.service';

@Component({
    selector: 'ngx-edit-person-modal',
    templateUrl: './edit-person-modal.component.html',
})
export class EditPersonModalComponent implements OnInit {

    modalHeader: string;
    selectedRow: IPerson = {
        name: '',
        personGroupId: '',
        userData: '',
        personId: '',
    };
    personForm = new FormGroup({
        personGroupId: new FormControl(),
        name: new FormControl(),
        userData: new FormControl(),
    })



    constructor(private activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: PersonService,
    ) {

    }
    ngOnInit() {
        this.createForm();
    }

    createForm() {
        this.personForm = this.fb.group({
            personGroupId: [{
                value: this.selectedRow.personGroupId,
                disabled: true,
            }, Validators.required],
            name: [this.selectedRow.name, Validators.required],
            userData: [this.selectedRow.userData, Validators.required],
        });
    }

    closeModal(message?: string) {
        this.activeModal.close(message);
    }

    save(form: NgForm) {
        const postBody = form.value;
        postBody.personGroupId = this.selectedRow.personGroupId;
        postBody.personId = this.selectedRow.personId;
        if (this.modalHeader.includes('Edit', 0)) {
            this.service.updatePerson(postBody).subscribe(data => {
                this.closeModal('Updated');
            });
        } else {
            this.service.createPerson(postBody).subscribe(data => {
                this.closeModal('Saved');
            });
        }
    }
}



