import { Component, OnInit } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { EditPersonModalComponent } from './modal/edit-person-modal.component';
import { PersonService, IPerson } from '../../../@core/data/person.service';
import { GroupService, IGroup } from '../../../@core/data/group.service';
import { FaceRenderComponent } from './face-render.component';
@Component({
    selector: 'ngx-person-list',
    templateUrl: './person-list.component.html',
    styleUrls: ['./person-list.component.scss'],
})
export class PersonListComponent implements OnInit {
    settings = {
        mode: 'external',
        add: {
            addButtonContent: '<i class="nb-plus"></i>',
            createButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        edit: {
            editButtonContent: '<i class="nb-edit"></i>',
            saveButtonContent: '<i class="nb-checkmark"></i>',
            cancelButtonContent: '<i class="nb-close"></i>',
        },
        delete: {
            deleteButtonContent: '<i class="nb-trash"></i>',
        },
        columns: {
            name: {
                title: 'Person Name',
                type: 'string',
            },
            userData: {
                title: 'Comments',
                type: 'string',
            },
            personId: {
                type: 'custom',
                renderComponent: FaceRenderComponent,
                filter: false,
                title: 'Faces Actionss',
            },
        },
    };

    source: LocalDataSource = new LocalDataSource();
    groupList: IGroup[];
    selectedGroup: IGroup;
    constructor(private service: PersonService,
        private modalService: NgbModal,
        private groupSvc: GroupService,
    ) {

    }

    ngOnInit() {
        this.getPersonList();
    }

    getPersonList() {
        if (this.selectedGroup) {
            this.service.getPersonList(this.selectedGroup.personGroupId).subscribe(person => {
                person.forEach(x => { x.personGroupId = this.selectedGroup.personGroupId });
                this.source.load(person);
            });
        } else {
            this.groupSvc.getGroup().subscribe(data => {
                this.groupList = data;
                this.selectedGroup = data[0];
                this.service.getPersonList(data[0].personGroupId).subscribe(person => {
                    person.forEach(x => { x.personGroupId = this.selectedGroup.personGroupId });
                    this.source.load(person);
                });
            });
        }
    }

    onGroupChange() {
        this.getPersonList();
    }

    onEdit(event): void {
        event.data.personGroupId = this.selectedGroup.personGroupId;
        const activeModal = this.modalService.open(EditPersonModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = 'Edit Person';
        activeModal.componentInstance.selectedRow = event.data;
        activeModal.result.then((data) => {
            if (data === 'Updated') {
                this.getPersonList();
            }
        }, (reason) => {
        });
    }

    onDelete(event): void {
        if (window.confirm('Are you sure you want to delete?')) {
            event.data.personGroupId = this.selectedGroup.personGroupId;
            const rowData: IPerson = event.data;
            this.service.deletePerson(rowData).subscribe(data => {
                this.getPersonList();
            });
        }
    }

    onCreate(event): void {
        // event.data.personGroupId = this.selectedGroup.personGroupId;
        const activeModal = this.modalService.open(EditPersonModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = 'Add New Person';
        activeModal.componentInstance.selectedRow = event.data;
        if (activeModal.componentInstance.selectedRow) {
            activeModal.componentInstance.selectedRow.personGroupId = this.selectedGroup.personGroupId;
        } else {
            activeModal.componentInstance.selectedRow = {};
            activeModal.componentInstance.selectedRow.personGroupId = this.selectedGroup.personGroupId;
        }

        activeModal.result.then((data) => {
            if (data === 'Saved') {
                this.getPersonList();
            }
        }, (reason) => {
        });
    }
}



