import { Component, Input, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { EditFaceModalComponent } from './modal/edit-face-modal.component';
import { ViewFaceModalComponent } from './modal/view-face-modal.component';


import { ViewCell } from 'ng2-smart-table';

@Component({
    template: `
    <button class="btn btn-secondary" (click)="addFace()">Tag Face</button>
    <button class="btn btn-secondary" (click)="viewFace()">View Faces</button>
  `,
})
export class FaceRenderComponent implements ViewCell, OnInit {

    renderValue: string;

    @Input() value: string | number;
    @Input() rowData: any;

    constructor(
        private modalService: NgbModal,
    ) {

    }

    ngOnInit() {
        this.renderValue = this.value.toString().toUpperCase();
    }

    addFace() {
        const activeModal = this.modalService.open(EditFaceModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = `Add [ ${this.rowData.name} ] Face in [ ${this.rowData.personGroupId} ]`;
        activeModal.componentInstance.selectedRow = this.rowData;
    }

    viewFace() {
        const activeModal = this.modalService.open(ViewFaceModalComponent, { size: 'lg', container: 'nb-layout' });
        activeModal.componentInstance.modalHeader = `View [ ${this.rowData.name} ] Face IDs in [ ${this.rowData.personGroupId} ]`;
        activeModal.componentInstance.selectedRow = this.rowData;
    }
}



